import React from 'react';
import LogIn from './LogIn';
const ContentLogin = ()=>{
    const handlemodal = ()=>{
        document.querySelector('.m-l').classList.remove('is-active')
    }
    return(<>
        <div class="modal m-l">
            <div class="modal-background"></div>
            <div class="modal-content">
                <LogIn  w="60%"/>
            </div>
            <button onClick={handlemodal} class="modal-close is-large" aria-label="close"></button>
        </div>
    </>)
}
export default ContentLogin;