import React from 'react';
const Registro = ()=>{
    return(
        <><form className="log" style={{width:"60%",margin:"auto",border:"2px solid white",position:"relative",borderRadius:"2em",padding:"2em"}}>
            <div class="field">
                <label style={{color:"#ffffff"}} class="label">Nombre</label>
                <div class="control">
                    <input class="input" type="text" placeholder="nombre"/>
                </div>
            </div>

            <div class="field">
                <label style={{color:"#ffffff"}} class="label">Nombre de usuario</label>
                <div class="control">
                    <input class="input" type="text" placeholder="Nombre de usuario"/>
                </div>
                <p class="help is-success">This username is available</p>
            </div>

            <div class="field">
                <label style={{color:"#ffffff"}} class="label">Email</label>
                <div class="control">
                    <input class="input" type="email" placeholder="Email"/>
                </div>
                <p class="help is-danger">This email is invalid</p>
            </div>
            <div class="field">
                <div class="control">
                    <label class="checkbox">
                    <input type="checkbox"/>
                       {"\u00a0"} acepte los <a href="#">terminos y condiciones</a>
                    </label>
                </div>
            </div>
            <div class="field is-grouped">
                <div class="control">
                    <button class="button is-link">Enviar</button>
                </div>
                <div class="control">
                    <button class="button is-link is-light">Cancel</button>
                </div>
            </div>
            </form>
        </>
    )
}
export default Registro;