import React,{lazy,Suspense} from 'react';
import NavBar from './components/nav/nav.bar';
import App2 from './components2/app2';
import Loader from './components2/section/Loader';
import {
  HashRouter,
  Switch,
  Route
} from "react-router-dom";

const App1 = lazy(()=>{
  return import('./components/app1');
})

function App() {
  return (
    <>
    <HashRouter>
    <Switch>
      <Route exact path="/">
        <NavBar background="#ffffff"/>
        <App2/>
      </Route>
      <Route exact path="/cv">
        <NavBar background="#00d1b2"/>
        <Suspense fallback={<Loader/>}>
          <App1/>
        </Suspense>
      </Route>
    </Switch>
    </HashRouter>
    </>
  );
}

export default App;
