import React from 'react';
import image from '../../medias/images/ilustracion14s.png';
import '../../css/title.css';
import '../../css/header.css';
import Nubes from '../section/Nubes';
import Contentregistro from '../section/Contentregistro';
import Contentlogin from '../section/ContentLogin';
const Header = ()=>{
    const handlemodallogin = ()=>{
        let toggle = false;
        toggle = !toggle;
        if(toggle){
            document.querySelector('.m-l').classList.add('is-active')
        }
    }
    const handlemodalregistro = ()=>{
        let toggle = false;
        toggle = !toggle;
        if(toggle){
            document.querySelector('.m-r').classList.add('is-active')
        }
    }
    return(
        <section className="hero" style={{background:"rgb(59, 5, 94)"}}>
            <br/>
            <br/>
            <div className="hero-body">
                <div className="columns is-mobile re">
                    <div className="column" style={{zIndex:"5"}}>
                    <br/>
                        <p className="title anm " style={{fontFamily: "'Press Start 2P', cursive",color:"white"}}>
                            Bienvenido a MS Academy 
                        </p>
                        <p className="title anm " style={{fontFamily: "",color:"white"}}>
                             Una plataforma donde aprendera desarrollo web fullstack de forma Gratuita,
                             sin tarjeta de credito, sin letras chiquitas
                        </p>
                
                        <br/>
                        <p className="subtitle">
                            <div className="control">
                                <button onClick={handlemodalregistro} style={{zIndex:"5"}} class="button is-primary is-rounded">Registrate Gratis</button>
                                {"\u00a0"}
                                {"\u00a0"}
                                {"\u00a0"}
                                <button onClick={handlemodallogin} style={{zIndex:"5"}} class="button is-primary is-rounded">Iniciar session</button>
                                <Contentregistro/>
                                <Contentlogin/>
                            </div>
                        </p>
                    </div>
                    <div className="column" style={{textAlign:"center"}}>
                        <img className="" src={image} style={{transform:"scale(1.1)"}} alt="programador"/>
                    </div>
                </div>
                <Nubes top="33%" left="20%" scale=".2" />
                <Nubes left="28%" scale=".2" top="30%" />
                <Nubes left="35%" scale=".2" top="20%" />
                <Nubes top="33%" left="2%" scale=".6"/>
            </div>
            <br/>
        </section>
    );
}
export default Header;