import React from 'react';
import pass from '../../medias/images/password.png';
import email from '../../medias/images/email.png';
import acad from '../../medias/images/academy (2).png';
const LogIn = ({w,color,som})=>{
    return(
        <>
        <form className="log" style={{boxShadow:som,borderRadius:"2em",padding:"2em"
          ,background:color
          ,width:w,height:"100%"
          ,margin:"auto"
          ,border:"2px solid white",position:"relative"}}>
        <img src={acad} style={{margin:"auto",display:"block"}} alt="ms-academy"/>
        <div className="field">
        <div className="control has-icons-left has-icons-right">
          <label className="label" style={{color:"white"}}>Correo</label>
          <input className="input" type="email" placeholder="Email" required/>
          <p class="help is-danger">This email is invalid</p>
          <span className="icon is-small is-left">
            <img className="fas" src={email} style={{position:"absolute",bottom:"-23px"}} alt="ms-email"/>
          </span>
          <span className="icon is-small is-right">
            <i className="fas fa-check"></i>
          </span>
        </div>
      </div>
      <div className="field">
        <div className="control has-icons-left">
        <label className="label" style={{color:"white"}}>Contraseña</label>
          <input className="input" type="password" placeholder="Password" required/>
          <p class="help is-danger">This email is invalid</p>
          <span className="icon is-small is-left">
          <img src={pass} style={{position:"absolute",bottom:"-20px"}} alt="ms-academy"/>
          </span>
        </div>
      </div>
      <div className="field">
        <div className="control">
          <button className="button  is-danger is-fullwidth is-outlined">
            Entrar
          </button>
        </div>
      </div>
      <hr/>
      </form>
      </>
    );
}
export default LogIn;