import React from 'react';
import Registro from './Registro';
const Contentregistro = ()=>{
    const handlemodal = ()=>{
        document.querySelector('.m-r').classList.remove('is-active')
    }
    return(<>
        <div class="modal m-r">
            <div class="modal-background"></div>
            <div class="modal-content">
                <Registro/>
            </div>
            <button onClick={handlemodal} class="modal-close is-large" aria-label="close"></button>
        </div>
    </>)
}
export default Contentregistro;